import API from "../../constants/api";
import { getWithPath, postWithPath, toJson } from "../http";

const AuthService = {
  postSession: (data) => {
    return postWithPath(API.AUTH.POST.SESSION, {}, data);
  },
  activeAccount: (data) => {
    return postWithPath(API.AUTH.POST.ACTIVE_ACCOUNT, {}, data);
  },
  getIp: () => {
    return window
      .fetch("https://api.bigdatacloud.net/data/client-ip")
      .then(toJson);
  },
  postDeviceInfo: () => {
    return postWithPath(API.NETWORK.GET.DEVICE);
  },
  getDeviceNetwork: (params) => {
    return getWithPath(API.NETWORK.GET.DEVICE, params);
  },
};
export default AuthService;
