import React, { useEffect, useState } from "react";
import Header from "../../../shared/components/header";
import { StatusConnection } from "./components/StatusConnection";
import NetworkInfo from "./components/NetworkInfo";
import { ACCOUNT_TYPE, CONNECTION_STATES } from "../../../constants/enum";
import EarnBoxInfo from "./components/EarnBoxInfo";
import { IconCheckSuccess } from "../../../assets/icon";
import AuthService from "../../../adapter/services/auth";
import ReferralLink from "./components/ReferralLink";
import InactiveScreen from "./components/InactiveScreen";
import classNames from "classnames";
import { Bounce, ToastContainer, toast } from "react-toastify";
import Loading from "../../../shared/components/loading";
import { useNavigate } from "react-router-dom";
import { ROUTER_EXTENSION } from "../../../constants/router";
import "./style.css";
import {
  ipState,
  networkInfoState,
} from "../../../adapter/recoil/common/state";
import { useRecoilValue } from "recoil";
import browser from "webextension-polyfill";
import { setRecoil } from "recoil-nexus";

const NetworkConnectionView = () => {
  const navigate = useNavigate();
  const ip = useRecoilValue(ipState);
  const [accountInfo, setAccountInfo] = useState<any>();
  const [networkAvailable, setNetworkAvailable] = useState<boolean>();
  const networkInfo = useRecoilValue(networkInfoState);
  const navigateToLogin = () => {
    navigate(ROUTER_EXTENSION.LOGIN);
  };

  browser.storage?.onChanged.addListener(async (changes) => {
    if (changes["status_ws"]) {
      let status: any = await browser.storage.local.get(["status_ws"]);
      if (status.status_ws == CONNECTION_STATES.CLOSED) {
        setNetworkAvailable(false);
      } else {
        setNetworkAvailable(networkInfo?.is_connected);
      }
    }
  });

  const getNetworkInfo = () => {
    AuthService.getDeviceNetwork({ ip })
      .then((res) => {
        if (res.data) {
          setRecoil(networkInfoState, res.data);
        } else {
          setRecoil(networkInfoState, undefined);
          setNetworkAvailable(false);
        }
      })
      .catch((error) => {
        setRecoil(networkInfoState, undefined);
        setNetworkAvailable(false);
      });
  };
  useEffect(() => {
    const intervalId = setInterval(() => {
      getNetworkInfo();
    }, 5000);

    return () => clearInterval(intervalId); //This is important
  }, []);

  useEffect(() => {
    setNetworkAvailable(networkInfo?.is_connected);
  }, [networkInfo]);

  useEffect(() => {
    AuthService.postSession({})
      .then((res) => {
        if (res.code === 0 && res.data) {
          setAccountInfo({
            ...res.data,
          });
        } else {
          navigateToLogin();
        }
      })
      .catch(() => {
        navigateToLogin();
      });
  }, []);

  const handleCopyLink = () => {
    toast.info(
      <span className="text-14px text-neutral-110 font-bold">
        Referral Link Copied
      </span>,
      {
        position: "top-center",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        transition: Bounce,
        icon: <img src={IconCheckSuccess} width={24} height={26} />,
        closeButton: false,
        className: "toast-noti-copy",
      }
    );
    navigator.clipboard.writeText(accountInfo?.referral_link);
  };

  const handleClickActivate = () => {
    AuthService.activeAccount({})
      .then((res) => {
        if (res.code === 0) {
          setAccountInfo({
            ...accountInfo,
            state: ACCOUNT_TYPE.ACTIVE,
          });
        } else {
          toast.error(res.msg, {
            position: "top-center",
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Bounce,
            closeButton: false,
          });
        }
      })
      .catch((error) => {
        toast.error(error.msg, {
          position: "top-center",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Bounce,
          closeButton: false,
        });
      });
  };
  if (!accountInfo) {
    return (
      <div className="layout">
        <Loading />
      </div>
    );
  }

  return (
    <div className="layout">
      <Header state={accountInfo?.state} avatar={accountInfo?.avatar} />
      <div>
        <div
          className={classNames({
            hidden: accountInfo?.state !== ACCOUNT_TYPE.INACTIVE,
          })}
        >
          <InactiveScreen onClick={handleClickActivate}></InactiveScreen>
        </div>
        <div
          className={classNames({
            hidden: !accountInfo,
          })}
        >
          <ReferralLink
            handleCopyLink={handleCopyLink}
            link={accountInfo?.referral_link}
          />
        </div>
        <div className="bg-[#F5F5F8] py-4 px-5">
          <div className="bg-white py-4 px-5 rounded-xl">
            <div className="flex justify-center">
              <StatusConnection isConnected={networkAvailable} />
            </div>
            <NetworkInfo
              score={networkInfo?.ip_score * 1}
              isConnected={networkAvailable}
            />
            <EarnBoxInfo points={accountInfo?.balance?.total_collected} />
          </div>
        </div>
      </div>
      <ToastContainer className="toast-notify" />
    </div>
  );
};

export default NetworkConnectionView;
